// HOMEPAGE HERO CAROUSEL 
if (document.querySelector('.hero-carousel')) {
    const heroCarousel = new Swiper(".hero-carousel", {
        modules: [A, P, N, Px, EC],
        slidesPerView: 1,
        speed: 1000,
        loop: false,
        resistance: true,
        resistanceRatio: 0,
        watchSlidesProgress: true,
        parallax: true,
        autoplay: {
            delay: 7000,
            disableOnInteraction: false,
            pauseOnMouseEnter: false,
        },
        navigation: {
            nextEl: ".hero-next",
            prevEl: ".hero-prev"
        },
        on: {
            slideChange: function () {
                let currentIndex = heroCarousel.activeIndex;
                let activeItem = document.querySelector('.carousel-item.is-active');
                if (activeItem) {
                    activeItem.classList.remove('is-active');
                }
                let newActiveItem = document.querySelectorAll('.carousel-item')[currentIndex];
                clearTimeout(this.slideChangeTimeout); // Clear previous timeout
                this.slideChangeTimeout = setTimeout(() => {
                    newActiveItem.classList.add('is-active');
                }, 1000);
            }
        },
    });
    let carouselItems = document.querySelectorAll('.carousel-item');
    carouselItems.forEach((item, index) => {
        item.addEventListener('click', () => {
            heroCarousel.slideTo(index, 1000, false);
            clearTimeout(heroCarousel.slideChangeTimeout); // Clear previous timeout
            heroCarousel.slideChangeTimeout = setTimeout(() => {
                item.classList.add('is-active');
            }, 1000);
            carouselItems.forEach((item, i) => {
                if (index !== i) {
                    item.classList.remove('is-active');
                }
            });
        });
    });
}



