// PADEL CLUB
document.addEventListener('DOMContentLoaded', function () {
    let clubButton = document.querySelectorAll('.club-button');
    let clupVideoButton = document.querySelectorAll('.clup-video-button');
    let clubImageButton = document.querySelectorAll('.clup-image-button');
    let slideVideo = document.querySelectorAll('.slideVideo');
    let slideImage = document.querySelectorAll('.slideImage');

    // SLIDE TYPE CHANGE FUNCTION
    clubButton.forEach((button) => {
        button.addEventListener('click', () => {
            if (button.classList.contains('clup-video-button')) {
                slideVideo.forEach((item) => {
                    item.classList.remove('hidden');
                });
                slideImage.forEach((item) => {
                    item.classList.add('hidden');
                });
            } else {
                slideVideo.forEach((item) => {
                    item.classList.add('hidden');
                });
                slideImage.forEach((item) => {
                    item.classList.remove('hidden');
                });
            }
            // Toggle active class
            clubButton.forEach((item) => {
                item.classList.remove('is-active');
            });
            button.classList.add('is-active');
        });
    });

    if (document.querySelector('.clup-carousel')) {
        let clupCarousel = new Swiper(".clup-carousel", {
            modules: [A, N, EC,],
            slidesPerView: 1,
            speed: 1000,
            spaceBetween: 0,
            loop: false,
            resistance: true,
            resistanceRatio: 0,
            watchSlidesProgress: true,
            effect: 'creative',
            creativeEffect: {
                prev: {
                    scale: 1.5,
                    opacity: 0,
                    shadow: true,
                },
                next: {
                    scale: 1.5,
                    opacity: 0,
                    shadow: true,
                },
            },
            // autoplay: {
            //     delay: 5000,
            //     disableOnInteraction: false,
            //     pauseOnMouseEnter: true,
            // },
            navigation: {
                nextEl: ".clup-next",
                prevEl: ".clup-prev"
            },
            on: {
                slideChange: function () {
                    let slideChangeOverlay = document.querySelectorAll('.clup-carousel .slideChangeOverlay');
                    slideChangeOverlay.forEach((item) => {
                        item.classList.add('is-active');
                    });

                    setTimeout(() => {
                        slideChangeOverlay.forEach((item) => {
                            item.classList.remove('is-active');
                        });
                    }, 1000);

                    if (document.querySelector('.clup-video-button.is-active')) {
                        slideImage.forEach((item) => {
                            item.classList.remove('is-active');
                        });
                        clubImageButton.forEach((item) => {
                            item.classList.add('is-active');
                        });
                    }
                    if (document.querySelector('.slideImage.hidden')) {
                        slideImage.forEach((item) => {
                            item.classList.remove('hidden');
                        });
                    }
                }
            },
        });
    }
});

