if (document.querySelector('.header-image-carousel')) {
    let i;
    let subMegaItems = document.querySelectorAll('.menu-wrapper .sub-menu .pages-field .item');
    const headerSlider = document.querySelectorAll('.header-image-carousel');
    let headerCarousels = [];

    for (i = 0; i < headerSlider.length; i++) {
        headerSlider[i].classList.add('header-image-carousel-' + i);

        headerCarousels[i] = new Swiper('.header-image-carousel-' + i, {
            modules: [A, P, N, Px],
            slidesPerView: 3,
            speed: 1000,
            spaceBetween: 30,
            loop: true,
            centeredSlides: true,
            resistance: true,
            resistanceRatio: 0,
            watchSlidesProgress: true,
            direction: "vertical",
            allowTouchMove: false,
        });
    }

    subMegaItems.forEach((item, index) => {
        item.addEventListener('click', () => {
            item.classList.add('is-active');
            subMegaItems.forEach((item, i) => {
                if (index !== i) {
                    item.classList.remove('is-active');
                }
            }
            );
            headerCarousels.forEach((carousel) => {
                carousel.slideTo(index, 1000, false);
            });
        });
    });

    if (document.querySelector('.header-content-carousel-hover')) {
        let i;
        const headerContentSliderHover = document.querySelectorAll('.header-content-carousel-hover');
        let headerContentCarouselsHover = [];
        let headerContentSliderSlides = document.querySelectorAll('.header-content-carousel-hover .swiper-slide');

        for (i = 0; i < headerContentSliderHover.length; i++) {
            headerContentSliderHover[i].classList.add('header-content-carousel-hover-' + i);

            headerContentCarouselsHover[i] = new Swiper('.header-content-carousel-hover-' + i, {
                modules: [A, P, N, Px, T],
                slidesPerView: 3,
                speed: 1000,
                spaceBetween: 30,
                loop: true,
                centeredSlides: false,
                resistance: true,
                resistanceRatio: 0,
                watchSlidesProgress: true,
                direction: "vertical",
                allowTouchMove: true,
            });
        }
        headerContentSliderSlides.forEach((item, index) => {
            item.addEventListener('mouseover', () => {
                headerCarousels.forEach((image) => {
                    image.slideTo(index, 1000, false);
                });
            });
        });
    }
}
