let menuItems = document.querySelectorAll('.menu-wrapper .menu .page-item');
let subMegaItems = document.querySelectorAll('.menu-wrapper .sub-menu .pages-field .header-content-carousel .item');


// MEGA MENU AMA FONKSYONU
menuItems.forEach((item, index) => {
    item.addEventListener("mouseover", () => {
        menuItems.forEach((menuItem, i) => {
            if (i === index) {
                menuItem.classList.add('is-active');
            } else {
                menuItem.classList.remove('is-active');
            }
        });
    });
});

body.addEventListener("click", (e) => {
    if (!e.target.closest('.menu-wrapper')) {
        menuItems.forEach((item) => {
            item.classList.remove('is-active');
        });
    }
});



// SUB MEGA OPEN MENU FUNCTION
subMegaItems.forEach((item, index) => {
    if (item.classList.contains('is-active')) {
        item.querySelector('.sub-pages').style.height = item.querySelector('.sub-pages').scrollHeight + 'px';
    } else {
        item.querySelector('.sub-pages').style.height = '0';
    }

    item.addEventListener('click', () => {
        subMegaItems.forEach((subItem, i) => {
            if (i === index) {
                subItem.classList.add('is-active');
                subItem.querySelector('.sub-pages').style.height = subItem.querySelector('.sub-pages').scrollHeight + 'px';
            } else {
                subItem.classList.remove('is-active');
                subItem.querySelector('.sub-pages').style.height = '0';
            }
        });
    });
});

