// SOCIAL ITEM PROGRESS BAR
import ProgressBar from 'progressbar.js';
let socialItems = document.querySelectorAll('.social-item');

socialItems.forEach(function (socialItem) {
    var bar = new ProgressBar.Circle(socialItem, {
        color: '#03246B',
        trailColor: '#eee',
        trailWidth: 3,
        duration: 1200,
        easing: 'bounce',
        strokeWidth: 6,
        from: { color: '#03246B', a: 0 },
        to: { color: '#75BF00', a: 1 },
        step: function (state, circle) {
            circle.path.setAttribute('stroke', state.color);
        }
    });

    socialItem.addEventListener('mouseover', function () {
        bar.animate(1.0);
    });
    socialItem.addEventListener('mouseleave', function () {
        bar.animate(0.0);
    });
});


let informationItems = document.querySelectorAll(".information-field li");

informationItems.forEach(function (informationItem) {
    informationItem.addEventListener("click", function () {
        let isActive = informationItem.classList.contains('is-active');

        informationItems.forEach(function (item) {
            item.classList.remove('is-active');
            item.querySelector('.expo-field').style.height = '0';
        });

        if (!isActive) {
            informationItem.classList.add('is-active');
            informationItem.querySelector('.expo-field').style.height = informationItem.querySelector('.expo-field').scrollHeight + 'px';
        }
    });

    if (informationItem.classList.contains('is-active')) {
        informationItem.querySelector('.expo-field').style.height = informationItem.querySelector('.expo-field').scrollHeight + 'px';
    }
});

