// PROJECT PAGE CHANGE MAP && MAP FUNCTION
let mapButton = document.querySelector('.map-button');
let projectMapField = document.querySelector(".project-map-field");
let projectItems = document.querySelectorAll('.project-item');

if (mapButton) {
    mapButton.addEventListener('click', function () {
        mapButton.classList.toggle('is-active');
        projectMapField.classList.toggle('is-active');
    });
}
let mapItems = document.querySelectorAll('.map-item');
let mapDots = document.querySelectorAll('.map-dot-wrapper .dot-field');

// CLICK EVENT
mapItems.forEach(item => {
    item.addEventListener('click', function () {
        mapItems.forEach(item => item.classList.remove('is-active'));
        this.classList.add('is-active');
        let mapItemsDataContinents = this.getAttribute('data-continent');
        mapDots.forEach(dot => {
            if (mapItemsDataContinents === 'all') {
                dot.classList.add('is-active');
                return;
            }
            let dotDataContinent = dot.getAttribute('data-continent');
            if (mapItemsDataContinents === dotDataContinent) {
                dot.classList.add('is-active');
            } else {
                dot.classList.remove('is-active');
            }
        });
        projectItems.forEach(item => {
            let itemDataContinent = item.getAttribute('data-continent');
            if (item) {
                item.classList.remove('active');
                if (mapItemsDataContinents === itemDataContinent) {
                    item.classList.add('active');
                } else if (mapItemsDataContinents === 'all') {
                    item.classList.add('active');
                }
                else {
                    item.classList.remove('active');
                }
            }
        });
    });
});

// DATA KEY
let mapPaths = document.querySelectorAll('.map-path');
mapPaths.forEach((path, index) => {
    path.addEventListener('mouseover', function () {
        mapPaths.forEach(otherPath => {
            if (otherPath !== path) {
                otherPath.classList.remove('is-active');
            }
        });
        path.classList.add('is-active');
    });

    let dataKey = path.getAttribute('data-key');
    let newDataKey = dataKey + index;
    path.setAttribute('data-key', newDataKey);
});

