// PRODUCT DETAIL TABS
let faqItems = document.querySelectorAll('.faq-item');
let faqTexts = document.querySelectorAll('.faq-text');
if (document.querySelector(".faq-item")) {
    faqItems.forEach((item) => {
        item.addEventListener('click', () => {
            faqItems.forEach((faqItem) => {
                if (faqItem !== item) { // Collapse other items
                    faqItem.classList.remove('is-active');
                    faqItem.querySelector('.faq-text').style.height = '0px';
                }
            });

            if (item.classList.contains('is-active')) { // Collapse clicked item if already active
                item.classList.remove('is-active');
                item.querySelector('.faq-text').style.height = '0px';
            } else { // Expand clicked item
                item.classList.add('is-active');
                let text = item.querySelector('.faq-text');
                text.style.height = text.scrollHeight + 'px';
            }
        });
    });
};
if (document.querySelector(".table-main-wrapper")) {
    // PRODUCT DETAIL TABLE FIX
    let tableMainWrapper = document.querySelector('.table-main-wrapper');
    let tableWrapper = document.querySelector('.table-wrapper');
    let table = tableMainWrapper.querySelector('table');

    tableWrapper.addEventListener('scroll', function () {
        let scrollLeft = tableWrapper.scrollLeft;
        let scrollWidth = table.scrollWidth;
        let clientWidth = tableWrapper.clientWidth;
        let scrollRight = scrollWidth - scrollLeft - clientWidth;

        if (scrollLeft > 0) {
            tableMainWrapper.classList.add('is-end');
        } else {
            tableMainWrapper.classList.remove('is-end');
        }

        if (scrollRight > 0) {
            tableMainWrapper.classList.remove('is-end');
        } else {
            tableMainWrapper.classList.add('is-end');
        }
    });

    // TABLE | WIDTH CHECK
    // ------------------------------------------------------------------
    if (document.querySelector('.table-wrapper')) {
        function tableWidthCheck() {
            let tableWrapperWidth = tableWrapper.offsetWidth;
            let tableWidth = table.offsetWidth;
            if (tableWidth > tableWrapperWidth) {
                tableMainWrapper.classList.add('is-show');
            } else {
                tableMainWrapper.classList.remove('is-show');
            }
            tableWidthCheck();
            window.addEventListener('resize', function () {
                tableWidthCheck();
            });
        }
    }
};

// BLOG ANIMATION
if (window.innerWidth > 1024) {
    let imageBoxs = document.querySelectorAll(".blog-field .blog-item");
    document.addEventListener('mouseover', (event) => {
        let imageBoxsArray = Array.from(imageBoxs);

        if (!imageBoxsArray.some(imageBox => imageBox.contains(event.target))) {
            imageBoxsArray.forEach(imageBox => {
                imageBox.classList.add("focus");
            });
        }
    });
    imageBoxs.forEach(imageBox => {
        imageBox.addEventListener('mouseover', () => {
            let imageBoxsArray = Array.from(imageBoxs);

            imageBoxsArray.forEach(otherImageBox => {
                if (otherImageBox !== imageBox) {
                    otherImageBox.classList.remove("focus");
                }
            });
            imageBox.classList.add("focus");
        });
        imageBox.addEventListener('mouseout', () => {
            let imageBoxsArray = Array.from(imageBoxs);

            imageBoxsArray.forEach(otherImageBox => {
                otherImageBox.classList.add("focus");
            });
        });
    });
}

