// MOBILE MENU
let mobileMenuOpenTrigger = document.querySelector('.open-mobile-menu-trigger');
let mobileMenuCloseTrigger = document.querySelector('.close-mobile-menu-trigger');
let mobileMenu = document.querySelector(".mobile-menu-field");
let mobileMenuItems = document.querySelectorAll(".mobile-menu-field .page");
let bgOverlayGeneralFooter = document.querySelector(".bg-overlay-general-footer");
mobileMenuOpenTrigger.addEventListener('click', function () {
    mobileMenuOpenTrigger.classList.add('in-active');
    mobileMenuCloseTrigger.classList.add('is-active');
    body.classList.add('overflow-hidden');
    mobileMenuOpenTrigger.classList.add('pointer-events-none');
    setTimeout(() => {
        mobileMenuOpenTrigger.classList.remove('pointer-events-none');
    }, 1500);
    mobileMenu.classList.add('menu-active');
    bgOverlayGeneralFooter.classList.add("active");
    bgOverlayGeneralFooter.classList.add("black");
});

mobileMenuCloseTrigger.addEventListener('click', function () {
    mobileMenuCloseTrigger.classList.add('pointer-events-none');
    setTimeout(() => {
        mobileMenuCloseTrigger.classList.remove('pointer-events-none');
    }, 1000);
    mobileMenuOpenTrigger.classList.remove('in-active');
    mobileMenuCloseTrigger.classList.remove('is-active');
    body.classList.remove('overflow-hidden');
    mobileMenu.classList.remove('menu-active');
    bgOverlayGeneralFooter.classList.remove("active");
    bgOverlayGeneralFooter.classList.remove("black");
    mobileMenuItems.forEach((item) => {
        item.classList.remove("is-active");
    })
});

mobileMenuItems.forEach((item) => {
    item.addEventListener('click', function () {
        mobileMenuItems.forEach((otherItem) => {
            if (otherItem !== item) {
                otherItem.classList.remove('is-active');
            }
        });
        item.classList.add('is-active');

        const subMenuItems = item.querySelectorAll('.sub-menu-item');
        subMenuItems.forEach((subItem) => {
            subItem.addEventListener('click', function (event) {
                subMenuItems.forEach((otherSubItem) => {
                    if (otherSubItem !== subItem) {
                        otherSubItem.classList.remove('is-active');
                    }
                });
                subItem.classList.toggle('is-active');
                item.classList.toggle('sub-active');
            });
        });
    });
});


