// ABOUT TAB
const items = document.querySelectorAll('.page-item');
const aboutTab = document.querySelector('.tab-section .page-field');

// Eer bir item seiliinde aboutTab'in boyutunu deitirmek istiyorsanz:
function toggleAboutTabSize() {
    aboutTab.classList.add('!h-[100px]');
}

items.forEach(item => {
    const scrollTargetSelector = item.getAttribute('data-scrollable');
    const scrollTarget = document.querySelector(scrollTargetSelector);

    if (scrollTarget) {
        const scrollTrigger = scrollTarget.offsetTop - 200;

        function setActiveItem() {
            items.forEach(item => {
                item.classList.remove('is-active');
            });
            item.classList.add('is-active');
        }

        function checkScrollPosition() {
            if (window.pageYOffset > scrollTrigger) {
                setActiveItem();
            }
            // Eer scroll hedefi en ste ulatnda 'aboutTab' elemanndaki snf kaldrmak istiyorsanz:
            if (window.pageYOffset === 0) {
                aboutTab.classList.remove('!h-[100px]');
            }
        }

        window.addEventListener('scroll', checkScrollPosition);

        // Eer item'a tklandnda aboutTab'in boyutunu deitirmek istiyorsanz:
        item.addEventListener('click', toggleAboutTabSize);
    }
});



// MAP ZOOM CONTROL
// Tm harita alanlarn sein
let mapWrappers = document.querySelectorAll('.map-wrapper');

// Her harita alan iin ilevi uygula
mapWrappers.forEach(function (mapWrapper) {
    let zoomIn = mapWrapper.querySelector('.zoom-in');
    let zoomOut = mapWrapper.querySelector('.zoom-out');
    let mapField = mapWrapper.querySelector('.map-image-field');

    let scale = 1; // Initial scale factor
    let zoomInCount = 0;
    let isDragging = false;
    let startX;
    let startY;
    let translateX = 0;
    let translateY = 0;

    zoomIn.addEventListener('click', function () {
        if (zoomInCount < 5) {
            scale *= 1.5; // Increase scale by 1.5 times
            mapField.style.transform = `scale(${scale}) translate(${translateX}px, ${translateY}px)`;
            mapField.style.transition = 'transform 0.5s';
            zoomInCount++;
        }
        if (zoomInCount > 0) {
            mapField.style.cursor = 'grab';
        }
    });

    zoomOut.addEventListener('click', function () {
        if (scale > 1) {
            scale /= 1.5; // Decrease scale by 1.5 times
            mapField.style.transform = `scale(${scale}) translate(${translateX}px, ${translateY}px)`;
            mapField.style.transition = 'transform 0.5s';
            zoomInCount = Math.max(zoomInCount - 1, 0); // Decrement zoomInCount, ensuring it never goes below 0
        }
        if (zoomInCount === 0) {
            mapField.style.cursor = 'default';
        }
    });

    mapField.addEventListener('mousedown', function (event) {
        if (zoomInCount > 0) {
            isDragging = true;
            startX = event.clientX;
            startY = event.clientY;
            mapField.style.cursor = 'grabbing';
        }
    });

    document.addEventListener('mouseup', function () {
        isDragging = false;
        mapField.style.cursor = 'grab';
    });

    document.addEventListener('mousemove', function (event) {
        if (isDragging) {
            let newX = event.clientX;
            let newY = event.clientY;
            translateX += newX - startX;
            translateY += newY - startY;
            mapField.style.transform = `scale(${scale}) translate(${translateX}px, ${translateY}px)`;
            startX = newX;
            startY = newY;
        }
    });

    mapField.addEventListener('mouseleave', function () {
        isDragging = false;
        mapField.style.cursor = 'grab';
    });
});