// ScrollReveal 
import ScrollReveal from 'scrollreveal';
window.ScrollReveal = ScrollReveal;
ScrollReveal({ afterReveal: function (el) { el.removeAttribute('style'); el.removeAttribute('data-sr-id'); } });
ScrollReveal().reveal('.srb', { interval: 20, distance: '30px', duration: 1000, viewFactor: 0.20, origin: 'bottom', mobile: false });
ScrollReveal().reveal('.srt', { interval: 20, distance: '30px', duration: 1000, viewFactor: 0.20, origin: 'top', mobile: false });
ScrollReveal().reveal('.srl', { interval: 20, distance: '30px', duration: 1000, viewFactor: 0.20, origin: 'left', mobile: false });
ScrollReveal().reveal('.srr', { interval: 20, distance: '30px', duration: 1000, viewFactor: 0.20, origin: 'right', mobile: false });
ScrollReveal().reveal('.srb-all > *', { interval: 120, distance: '60px', duration: 1000, viewFactor: 0.25, origin: 'bottom', mobile: false });
ScrollReveal().reveal('.srb-all-fast > *', { interval: 30, distance: '60px', duration: 1000, viewFactor: 0.25, origin: 'bottom', mobile: false });
ScrollReveal().reveal('.srl-all > *', { interval: 120, distance: '60px', duration: 1000, viewFactor: 0.25, origin: 'left', mobile: false });
ScrollReveal().reveal('.srr-all > *', { interval: 120, distance: '60px', duration: 1000, viewFactor: 0.25, origin: 'right', mobile: false });
ScrollReveal().reveal('.srt-all > *', { interval: 120, distance: '60px', duration: 1000, viewFactor: 0.25, origin: 'top', mobile: false });

