// SCROLLABLE SELECTOR CLICK
// .scrollable class' bulunan bir objeye tklaynca, "data-scrollable" alannda belirtilen alana smooth bir ekilde scroll eder.
/* rnek Kullanm
    <p class="cursor-pointer scrollable" data-scrollable="#section-1">Go to section 1</p>
    <section id="section-1"></section>
*/
// ------------------------------------------------------------
if (document.querySelector('.scrollable')) {
    document.querySelectorAll(".scrollable").forEach((element, index) => {
        element.addEventListener("click", () => {
            let scrollableSection = element.getAttribute('data-scrollable');
            gsap.to(window, { duration: .65, scrollTo: { y: scrollableSection, offsetY: 10 } });
        });
    });
}

// PROJECT BASED |  GSAP SCRIPTS
