// ROTATE ANIMATION
// RECTANGLE CLASS SCRIPT
// Rotate animasyonlar
const rotateElements = document.querySelectorAll(".rotate-anim");
const animContainers = document.querySelectorAll(".animContainer");

rotateElements.forEach((element) => {
    animContainers.forEach((container) => {
        gsap.to(element, {
            y: '150%',
            rotation: 360,
            duration: 1,
            ease: "none",
            scrollTrigger: {
                trigger: element,
                start: "top top+=30%",
                end: "bottom top",
                scrub: 1,
                markers: false,
            }
        });
    });
});

// x-anim-container'lar iin animasyon
const xAnimContainers = document.querySelectorAll(".x-anim-container");

xAnimContainers.forEach((container) => {
    gsap.to(container, {
        scrollTrigger: {
            trigger: container,
            start: "center 60%",
            end: "bottom 20%",
            toggleActions: "play none none none",
            markers: false,
            onUpdate: function () {
                container.classList.add("is-active");
            }
        }
    });

    const elementsInContainer = container.querySelectorAll(".rotate-anim-x");
    elementsInContainer.forEach((element) => {
        gsap.to(element, {
            x: '-200%',
            rotation: 360,
            duration: 1,
            ease: "none",
            scrollTrigger: {
                trigger: container,
                start: "top bottom",
                end: "center center",
                scrub: 1,
                markers: false,
            },
            onComplete: () => {
                element.classList.add('is-active');
            }
        });
    });
});

// Dier animasyonlar
animContainers.forEach((el) => {
    let tl = gsap.timeline({
        scrollTrigger: {
            trigger: el,
            start: "center 60%",
            end: "bottom 20%",
            toggleActions: "play none none none",
            markers: false,
            onUpdate: function () {
                el.classList.add("is-active");
            }
        },
    });
    tl.to(el, {
        opacity: 1,
        duration: 1
    });
});

