// RECTANGLE CLASS SCRIPT
let mvItems = document.querySelectorAll(".mv-item");
mvItems.forEach((element) => {
    let rightShape = element.querySelector(".right-triangle");
    let rightTl = gsap.timeline({ paused: true, repeat: -1 });
    rightTl.to(rightShape, {
        x: '-652%',
        rotate: 360,
        duration: 5,
        ease: "none",
        onComplete: () => {
            rightTl.restart();
        }
    });

    let leftShape = element.querySelector(".left-triangle");
    let leftTl = gsap.timeline({ paused: true, repeat: -1 });
    leftTl.to(leftShape, {
        x: '700%',
        rotate: 360,
        duration: 5,
        ease: "none",
        onComplete: () => {
            leftTl.restart();
        }
    });

    element.addEventListener("mouseover", () => {
        rightTl.play();
        leftTl.play();
    });

    element.addEventListener("mouseleave", () => {
        rightTl.pause(0);
        leftTl.pause(0);
    });
});


// Hover Repulse Effect
gsap.utils.toArray(".fx-hover-repulse").forEach(element => {
    element.addEventListener("mouseenter", () => { element.classList.add('moveable') }, false);
    element.addEventListener("mouseleave", () => {
        element.classList.remove('moveable')
        let inner = element.querySelector('.inner');
        let icon = element.querySelector('.inner .item');

        gsap.to(inner, { duration: 0.45, x: 0, y: 0 });
        gsap.to(icon, { duration: 0.45, x: 0, y: 0 });
    }, false);
    element.addEventListener("mousemove", (e) => {
        if (element.classList.contains('moveable')) {
            let inner = element.querySelector('.inner');
            let icon = element.querySelector('.inner .item');

            let rect = e.target.getBoundingClientRect();
            let x = e.clientX - rect.left - (element.offsetWidth / 2);
            let y = e.clientY - rect.top - (element.offsetHeight / 2);

            let setPositionX = gsap.quickTo(inner, "x", { duration: 0.1, }),
                setPositionY = gsap.quickTo(inner, "y", { duration: 0.1, });

            let setPositionIconX = gsap.quickTo(icon, "x", { duration: 0.2, }),
                setPositionIconY = gsap.quickTo(icon, "y", { duration: 0.2, });

            setPositionX(x / 5)
            setPositionY(y / 5)
            setPositionIconX(x / 3)
            setPositionIconY(y / 3)
        }
    }, false);
});


// Hover Text Effect - With Child
gsap.utils.toArray(".fx-text-hover-with-child").forEach(element => {
    document.fonts.ready.then(() => {
        let text = element.querySelector(".fx-child").textContent;
        element.querySelector(".fx-child").innerHTML = '<span class="actual pointer-events-none -translate-x-[50%] -translate-y-[50%] absolute left-[50%] top-[50%] w-[100%]">' + text + '</span><span class="placeholder pointer-events-none">' + text + '</span>';

        let placeholder = new SplitText(element.querySelector(".placeholder"), { type: "words, lines", wordsClass: 'split-words', linesClass: 'split-lines' });
        let actual = new SplitText(element.querySelector(".actual"), { type: "words, lines", wordsClass: 'split-words', linesClass: 'split-lines' });

        let textHoverTL = gsap.timeline({ paused: true });

        gsap.set(actual.words, { y: '75%', opacity: 0, });

        textHoverTL.to(placeholder.words, { duration: 0.25, y: '-75%', opacity: 0, })
            .to(actual.words, { delay: 0.025, duration: 0.25, y: '0%', opacity: 1, }, 0);

        element.addEventListener("mouseenter", () => { textHoverTL.play() }, false);
        element.addEventListener("mouseleave", () => { textHoverTL.reverse() }, false);
    });
});

// STICKY MODULE
let stickyElements = document.querySelectorAll('.sticky-module');
if (innerWidth > 1024) {
    stickyElements.forEach((element) => {
        gsap.to(element, {
            scrollTrigger: {
                trigger: element,
                start: "top top",
                end: "bottom top",
                pin: true,
                scrub: 1,
                markers: false,
                onUpdate: function () {
                    element.classList.add("!top-[120px]");
                }
            }
        });
    }
    );
}